import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"

const CourseRating = ({ pageContext: { course } }) => (
  <Layout>
    <h2>{course.name}</h2>
    <p>{course.breed}</p>
    {course.name} - {course.breed}

  </Layout>

)

export default CourseRating

